*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
.heading{
  width:100%;
  line-height: 100px;

  background-image: linear-gradient(#2c3e50,#000000);
  color:#fff;
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 900;

}

.searchbar{
  width: 100%;
  height: auto;
  margin: 0 auto;
  text-align: center;
  margin-top: 5%;
}

.searchbar input{
  margin: 30px 0;
  padding: 5px 10px;
  border-radius: 5px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Couries New";
}